<template>
  <div>
  <v-container class="text-center">          
    <img src="../assets/logo200x200.png" alt="" width="150px">
    <h2> TROD APP </h2>
    <h4>correo: support@trodapp.com </h4>
    <h4>número: +1 (888) 2206679 </h4>
  </v-container>
  
  <v-container class="text-center">
      <v-card class="ma-auto" flat>
        <v-card-title>
            Enter your new password.
          </v-card-title>
          <v-text-field
            class="ma-2"
            placeholder="*Password"
            required
            outlined
            dense
            v-model="user.new_password"
            type="password"
          >
          </v-text-field>
          <v-text-field
            class="ma-2"
            placeholder="*Confirm password"
            required
            outlined
            dense
            v-model="user.confirm_password"
            type="password"
          >
          </v-text-field>
          <v-divider></v-divider>
          <v-btn color="#E47911" dark class="ma-2" large :loading="loading" @click="send_password">
            Send
          </v-btn>
      </v-card>
  </v-container>
  </div>
</template>

<script>
  import UserAuthService from '@/services/UserAuthService';
  import Swal from 'sweetalert2';

export default {

    data: () => ({
      loading: false,
      user: {
        unique_id: '',
        new_password: '',
        confirm_password: ''
      }
    }),

    computed: {
    idToken(){
      return this.$route.params.id
    }
  },

  mounted(){
    UserAuthService
    .check_token(this.idToken)
    .then(res => {
      this.user.unique_id = res.user
    })
    .catch(e => {
      this.loading = false;
      let error = e.response.data;
      let msg = ''
      Object.entries(error).forEach(([ key, value ]) => {
          msg+= key + ': ' + value + ' \n ';
      });
          Swal.fire({
          icon: 'error',
          title: 'Error',
          text: msg
          }).then(() => {
              this.$router.push('/')
          })
      })
  },

  methods: {

    send_password(){
      this.loading = true
      UserAuthService
      .change_password(this.user)
      .then(res => {
          console.log(res)
          Swal.fire({
          icon: 'success',
          title: 'Success',
          text: 'The new password has been sent.'
          }).then(() => {
              this.loading = false
              this.$router.go(-1)
          })
      })
      .catch(e => {
      this.loading = false;
      let error = e.response.data;
      let msg = ''
      Object.entries(error).forEach(([ key, value ]) => {
          msg+= key + ': ' + value + ' \n ';
      });
          Swal.fire({
          icon: 'error',
          title: 'Error',
          text: msg
          });
      })
    }

  }


}
</script>